import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {Navbar, Nav,Container ,Button,Form, NavDropdown } from 'react-bootstrap'
import {LinkContainer} from 'react-router-bootstrap';
import { logout } from '../actions/userActions'
import { Search } from 'tabler-icons-react';

function Header(){
  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  const dispatch = useDispatch()

  const logoutHandler = () =>{
    //dispatch(logout())
    logout()
    window.location.reload()
  }


  return (
    <header>
      
      <Navbar bg="dark" variant='dark' expand='lg'>
      <Container>
        <LinkContainer to='/'>
        <Navbar.Brand >Prorecrute</Navbar.Brand>
        </LinkContainer>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
          <LinkContainer to='/register'>
            <Nav.Link ><i className='fas fa-user'> Register</i></Nav.Link>
            </LinkContainer>

            {userInfo ? (
              <NavDropdown title={userInfo.name} id='username'>
                <LinkContainer to='/profile'>
                  <NavDropdown.Item>Profile</NavDropdown.Item>
                </LinkContainer>

                <NavDropdown.Item onClick={logoutHandler}>Logout</NavDropdown.Item>
              </NavDropdown>
            ):(
              <LinkContainer to='/login'>
            <Nav.Link ><i className='fas fa-user'> Login </i></Nav.Link>
            </LinkContainer>
            )}

            
          </Nav>
          <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="dark"><Search/></Button>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    </header>
  );
}
export default Header;
