import {
    EMPLOI_LIST_REQUEST,
    EMPLOI_LIST_SUCCESS,
    EMPLOI_LIST_FAIL,
    
    EMPLOI_DETAILS_REQUEST,
    EMPLOI_DETAILS_SUCCESS,
    EMPLOI_DETAILS_FAIL
    
} from '../constants/emploiConstants'


export const emploiListReducer = (state = {emplois:[]}, action) => {
    switch(action.type){
        case EMPLOI_LIST_REQUEST:
            return {loading:true, emplois :[]}
        case EMPLOI_LIST_SUCCESS:
            return {loading:true, emplois:action.payload }
        case EMPLOI_LIST_FAIL:
            return { loading:false, error: action.payload }
        default:
            return state

    }
}
export const emploiDetailsReducer = (state = {emploi:{}}, action) => {
    switch(action.type){
        case EMPLOI_DETAILS_REQUEST:
            return {loading:true, ...state}
        case EMPLOI_DETAILS_SUCCESS:
            return {loading:true, emplois:action.payload }
        case EMPLOI_DETAILS_FAIL:
            return { loading:false, error: action.payload }
        default:
            return state

    }
}

