import React, {useState, useEffect} from 'react';
import { Row } from 'react-bootstrap';
import Emploi from '../components/Emploi.js';
import { useDispatch, useSelector } from 'react-redux'
import { listEmplois } from '../actions/emploiActions'
import Message from '../components/Message'


function HomeScreen(){
    const dispatch = useDispatch()
    const emploiList = useSelector(state => state.emploiList)
    const {error,loading, emplois} = emploiList

    useEffect(() => {
        dispatch(listEmplois())

    },[dispatch]);
    
    return (
        <div>
            <h1 style={{marginTop:20}}>Latest posts</h1>
            {error ? <Message variant='danger'>{error}</Message>
                :
                <Row>
                    {emplois.map(emploi => (
                        <div key={emploi._id} sm={12} md={6} lg={4} xl={3}>
                            <Emploi emploi={emploi} />
                        </div>
                    ))
                    }
                </Row>   
            }
               
            
        </div>
    );
}
export default HomeScreen;