import React, { useState, useEffect }from 'react'
import { Link, useParams } from 'react-router-dom';
import { Row, Col, ListGroup, Button, Card, ListGroupItem} from 'react-bootstrap'
import axios from 'axios'
import { LinkContainer } from 'react-router-bootstrap';

function EmploiScreen() {

        const {id} = useParams();
        const [emploi, setEmploi] = useState([])

        useEffect(() => {
            async function fetchEmploi(){
                const { data } = await axios.get(`https://prorecrute.herokuapp.com/api/emplois/${id}`)
                setEmploi(data)
            }
            fetchEmploi()
        },[]);
    return (
        <div>
            <Row>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <h3 style={{marginTop:20}}>{emploi.title}</h3>
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Date : {emploi.date}
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Entreprise : {emploi.company}
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Description : 
                        <div>{emploi.description}</div>
                        
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Localisé à :
                        <div>{emploi.location}</div>
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Missions : 
                        <div>{emploi.mission}</div>
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Profil recherché : 
                        <div>{emploi.profil}</div>
                    </ListGroup.Item>

                    <ListGroup.Item>
                        Critères : 
                        <div>{emploi.critere}</div>
                    </ListGroup.Item>
                    <Card>
                    <ListGroupItem>
                        <LinkContainer to='/form'>
                            <Button className='btn-block' type='button' variant="dark"> Postuler </Button>
                        </LinkContainer>
                    </ListGroupItem>
                    </Card>
                </ListGroup>
            </Row> 
        </div>
    );
    }

export default EmploiScreen;