import React,{ useState,useEffect, useInsertionEffect } from 'react'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import {Container,InputGroup,LinkContainer , Button} from 'react-bootstrap'
import Message from '../components/Message'


function FormScreen() {
    const navigate = useNavigate()
    const [emploiNames, setEmploiNames] = useState([])
    const [firstname, setFirstname] = useState("");
    const [lastname, setLastname] = useState("");
    const [tel, setTel] = useState("");
    const [email, setEmail] = useState("");
    const [poste, setPoste] = useState("");
    const [error, setError] = useState(false);
    const [message, setMessage] = useState("")
    const [variant, setVariant] = useState("")

    useEffect(() => {
        if(!localStorage.getItem("userInfo")) navigate("/login")
        fetch("https://prorecrute.herokuapp.com/api/emplois/")
        .then(res => res.json())
        .then(res => {
            setEmploiNames(res.map(obj => obj.title))
            setPoste(res.map(obj => obj.title)[0])
        })
    }, [])
    const HandlePostuler = () => {
        fetch('https://prorecrute.herokuapp.com/api/candidats/',{
            method: "POST",
            headers: {
                "Content-Type" : 'application/json'
            },
            body: JSON.stringify({
                firstname: firstname,
                lastname: lastname,
                tel: tel,
                email: email,
                poste: poste,
            })
        })
        .then(res => res.json())
        .then(res => {
            if(res.status===200)
            {
                setMessage("Votre postulation a été enreg")
                setVariant("success")
                setError(true)
            }
                
            else
            {
                setMessage("Invalid infos");
                setVariant("danger")
                setError(true)
            }
                
        })
    }
    
    
  return (
        <div>
          <Container>
            <h1 style={{marginTop:20}}>Form</h1>
            {
                error ? <Message variant={variant}>{message}</Message> : null
            }
          <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="First Name" value={firstname} onChange={(e) => {setFirstname(e.target.value)}} ></input>
            <label for="floatingInput">First Name</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="Last Name" value={lastname} onChange={(e) => {setLastname(e.target.value)}}></input>
            <label for="floatingInput">Last Name</label>
        </div>
        <div class="form-floating mb-3">
        <input type="tel" class="form-control" id="floatingInput" placeholder="Tel" value={tel} onChange={(e) => {setTel(e.target.value)}}></input>
        <label for="floatingInput">Telephone</label>
        </div>
        <div class="form-floating mb-3">
            <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com" value={email} onChange={(e) => {setEmail(e.target.value)}}></input>
            <label for="floatingInput">Email address</label>
        </div>
        <select class="form-select" aria-label="Default select example" value={poste} onChange={(e) => {setPoste(e.target.value)}}>
            {
                emploiNames.map(emp => (
                    <option>{emp}</option>
                ))
            }
            
         </select>
    <InputGroup className="mb-3">
     
        <div class="mb-3" style={{marginTop:20}}>
        <label for="formFile" class="form-label">Select an Image</label>
        <input class="form-control" type="file" id="formFile"></input>
        </div>
        <div class="mb-3" style={{marginLeft: 20, marginTop:20}}>
        <label for="formFile" class="form-label">Select a CV</label>
        <input class="form-control" type="file" id="formFile"></input>
        </div>
    </InputGroup>
            <Button
                onClick = {
                    HandlePostuler 
                }
            
            > Postuler
            </Button>

            

          </Container>
        </div>
  )
}

export default FormScreen;