import axios from 'axios'

import {
    EMPLOI_LIST_REQUEST,
    EMPLOI_LIST_SUCCESS,
    EMPLOI_LIST_FAIL,

    EMPLOI_DETAILS_REQUEST,
    EMPLOI_DETAILS_SUCCESS,
    EMPLOI_DETAILS_FAIL
} from '../constants/emploiConstants'


export const listEmplois = () => async (dispatch) => {
   try{
    dispatch({type : EMPLOI_LIST_REQUEST})
    const { data } = await axios.get('https://prorecrute.herokuapp.com/api/emplois/');

    dispatch({
        type : EMPLOI_LIST_SUCCESS,
        payload : data
    })

   }catch(error){
    dispatch({
        type : EMPLOI_LIST_FAIL,
        payload : error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
    })
   }
}

export const listEmploiDetails = (id) => async (dispatch) => {
    try{
     dispatch({type : EMPLOI_DETAILS_REQUEST})
     const { data } = await axios.get(`https://prorecrute.herokuapp.com/api/emplois/${id}`);
 
     dispatch({
         type : EMPLOI_DETAILS_SUCCESS,
         payload : data
     })
 
    }catch(error){
     dispatch({
         type : EMPLOI_DETAILS_FAIL,
         payload : error.response && error.response.data.message
             ? error.response.data.message
             : error.message,
     })
    }
 }